/* -------------------------------------------------------------------------- */

.documentation-page .cardgrid-link svg {
  display: block;
}

.documentation-page svg * {
  stroke: #cca000;
}

.documentation-page .h6 {
  position: relative;
  padding-bottom: .5rem;
}

.documentation-page .h6:after {
  position: absolute;
  bottom: -.125rem;
  left: 0;
  content: "";
  height: 4px;
  background: #ccc;
  width: 2rem;
}




